<template>
	<template
		v-if="isEnd">
		<span class="transaction transaction_successful"
			v-if="isStatus"
		>Transaction was successful</span>

		<span class="transaction transaction_failed"
			v-if="!isStatus"
		>Transaction failed</span>
	</template>

	<template
		v-if="!isEnd"
	>
		<div class="popup__top d-flex">
			<div class="counter-elem__arrow counter-elem__minus"
				@click="orderCountMinus"
			><img src="/img/minus.svg" alt=""></div>
			<div class="counter-elem__input splash-input">{{ orderCount }}</div>
			<div class="counter-elem__arrow counter-elem__plus"
				@click="orderCountPlus"
			><img src="/img/plus.svg" alt=""></div>
			<p class="counter-elem__max"
				@click="orderCount = config.MAXIMUM_NFT"
			>max</p>
		</div>

		<p class="popup__name">Your order</p>

		<div class="popup__block">
			<div class="popup__row d-flex">
				<p class="popup__text">{{ contractPrice }} ETH x {{ orderCount }}</p>
				<div class="popup__content">
					<p class="popup__info">{{ allPrice }} ETH</p>
					<p class="popup__sum">~ ${{ fixedPrice(priceUSD * allPrice) }}</p>
				</div>
			</div>

			<div class="popup__row d-flex">
				<p class="popup__text">Manifold Fee x {{ free }}</p>
				<div class="popup__content">
					<p class="popup__info">{{ freePrice }} ETH</p>
					<p class="popup__sum">~ ${{ fixedPrice(priceUSD * freePrice) }}</p>
				</div>
			</div>

			<div class="popup__bottom">
				<div class="popup__row d-flex">
					<p class="popup__total">Total</p>
					<div class="popup__content">
						<p class="popup__info">{{ totalPrice }} ETH</p>
						<p class="popup__sum">~ ${{ fixedPrice(priceUSD * totalPrice) }}</p>
					</div>
				</div>

				<div class="popup__flex d-flex">
					<p class="popup__buy">Buying As <a href="#">{{ shortHash(userId) }}</a></p>
					<button class="popup__btn btn btn_disc"
						@click="$emit('disconnect')"
					>
						<p>Disconnect</p>
					</button>
				</div>
			</div>

			<button class="popup__link btn"
				:class="{ disabled: inProcess }"
				@click="buy"
			><p>Buy now</p></button>
		</div>
	</template>
</template>

<script>
import config from '@/config'
import { walletShorting, fixedPrice } from '@/functions'
import wallets from '@/wallets/wallets'

export default {
	name: 'CheckoutComp',

	props: {
		userId: String,
		contractPrice: String,
		mintQuant: String,
		triggerPopup: Boolean,
	},

	emits: ['disconnect', 'close-me'],

	data(){
		return {
			orderCount: 1,
			config: config,
			priceUSD: 1719,
			inProcess: false,
			isEnd: false,
			isStatus: false,
		}
	},

	methods: {
		orderCountPlus(){
			if(this.orderCount >= config.MAXIMUM_NFT) {
				this.orderCount = config.MAXIMUM_NFT
				return
			}
			this.orderCount++
		},

		orderCountMinus() {
			if(this.orderCount <= 1) {
				this.orderCount = 1
				return
			}
			this.orderCount--
		},

		fixedPrice(summ) {
			return fixedPrice(summ)
		},

		shortHash(string) {
			return walletShorting(string)
		},

		async buy() {
			if(this.inProcess) return

			this.inProcess = true
			let transactionStatus = await wallets.mint(this.orderCount, this.allPrice)

			if(!transactionStatus) {
				console.error('Transaction fail')
			}
			this.isStatus = transactionStatus
			this.isEnd = true
			this.inProcess = false
			setInterval(() => {
				this.$emit('close-me')
			},5000)
		}
	},

	watch: {
		mintQuant(){
			this.orderCount =  Number(this.mintQuant)
		},

		triggerPopup() {
			setTimeout(()=>{
				this.orderCount =  Number(this.mintQuant)
				this.isStatus = false
				this.isEnd = false
			}, 300)
		}
	},

	computed: {
		allPrice() {
			return this.fixedPrice(Number(this.contractPrice) * this.orderCount)
		},

		free() {
			let free = 0
			if(this.orderCount >= 10) free = Math.round(this.orderCount / 5)
			return free
		},

		freePrice() {
			return this.fixedPrice(Number(this.contractPrice) * this.free)
		},

		totalPrice() {
			let count = this.orderCount - this.free
			return this.fixedPrice(Number(this.contractPrice) * count)
		},
	}
}
</script>