export const walletShorting = (string, firstPart = 6) => {
	if(!string) {
		return null
	}
	const length = string.length

	if(length < firstPart + 8) {
		return string
	}
	const shortString = string.substring(0, firstPart) + '...' + string.substring(length - 6, length)
	return shortString
}

export const fixedPrice = (summ, count = 18) => {
	return (summ > 0) ? summ.toFixed(count).replace(/0+$/,'') : 0
}