<template>
	<div class="popup__bg"
		:class="{active: isOpen}"
		@click="close"
	>
		<div class="popup"
			:class="{active: isOpen}"
			@click.stop
		>
			<img src="/img/close.svg" alt="" class="close-popup"
				@click="close"
			>
			<div class="popup__wrap">
				<p class="popup__title">{{ popupTitle }}</p>
				<slot></slot>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'popup-comp',

	props: {
		popupTitle: {
			type: String,
			default: ''
		},
		isOpen: Boolean,
	},

	emits: [
		'update:isOpen',
		'close'
		],

	methods: {
		close() {
			this.$emit('update:isOpen')
			this.$emit('close')
		}
	}
}
</script>