<template>
  <div class="body-wrap mint">
		<header class="header mint" id="header">
			<nav class="nav" id="nav">
        <NavComp
          :is-mint="true"
          :is-connect="isConnect"
          @connect-status="setConnectStatus"
        />
      </nav>

			<div class="container mint__container">
        <div class="mint__box d-flex">
					<picture>
						<source :srcset="`/img/ran/${mainImageId}.webp, /img/ran/${mainImageId}@2.webp 2x`" type="image/webp">
						<source :srcset="`/img/ran/${mainImageId}.png, /img/ran/${mainImageId}@2.png 2x`" type="image/png">
						<img :src="`/img/ran/${mainImageId}.png`" alt="" class="mint__pic">
					</picture>

          <div class="mint__content">
            <div class="mint__top d-flex">
              <div class="mint__left">
                <h1>Foxtrove nft #013567</h1>
                <p class="mint__el">A unique collection of 10,000 NFTs created with love and creativity by our team</p>
              </div>

              <a href="#" class="mint__subscribe d-flex">
                <svg class="icon"><use xlink:href="/img/svg-sprite.svg#love"></use></svg>
                <p>Subscribe</p>
              </a>
            </div>

            <div class="mint__info">
              <p class="mint__name">Batch discount:</p>
              <p class="mint__sub">Mint 5, get 1 free airdropped</p>
              <div class="cont_select_center">
                <select class="js-choice" ref="choice"
                  v-model="mintQuant"
                >
                  <option value="1" placeholder>Mint {{ freeAirdropped[0].mint }}, get {{ freeAirdropped[0].free }} free airdropped etc.</option>
                  <option
                    v-for="option in freeAirdropped"
                    :key="option.mint"
                    :value="option.mint"
                  >Mint {{ option.mint }}, get {{ option.free }} free airdropped</option>
                </select>
              </div>
            </div>

            <div class="mint__row d-flex">
              <div class="mint__block">
                <p class="mint__name">limited edition</p>

                <form action="#" class="mint__form">
                  <div class="mint__flex d-flex">
                    <div class="mint__item">
                      <label>Price</label>
                      <input type="text" class="mint__input" name="price"
                        :value="totalPrice"
                      >
                    </div>

                    <div class="mint__item">
                      <label>Total Minted</label>
                      <input type="text" class="mint__input" name="total"
                        :value="`${contractMinted ? contractMinted : config.defaultTotalMinted} / 10000`"
                      >
                    </div>
                  </div>

                  <a href="#" class="mint__button btn"
                    v-if="isConnect"
                    @click="buyNow"
                  ><p>Buy now</p></a>

                  <button class="mint__button btn"
                    v-if="!isConnect"
                    @click="connectWallet"
                  ><p>Connect wallet</p></button>
                </form>
              </div>

              <div class="mint__rem">
                <img src="/img/last-arr.svg" alt="">
                <p>Remember, if you have a FoxTrove NFT, you will receive a token worth $100.(1.300.000 Foxcoin)</p>
              </div>
            </div>

            <div class="mint-bottom d-flex">
              <div class="mint-bottom__item">
                <p class="mint-bottom__name">ARWEAVE URL</p>
                <a href="" class="mint-bottom__text">LZ4ggsds9...ffQ89Y</a>
              </div>

              <div class="mint-bottom__item">
                <p class="mint-bottom__name">CONTRACT ADDRESS</p>
                <a href="" class="mint-bottom__text">LZ4ggsds9...ffQ89Y</a>
              </div>

              <div class="mint-bottom__item">
                <p class="mint-bottom__name">TOKEN STANDART</p>
                <p class="mint-bottom__text">ERC721</p>
              </div>

              <div class="mint-bottom__item">
                <p class="mint-bottom__name">BLOCKCHAIN</p>
                <p class="mint-bottom__text">ETHEREUM</p>
              </div>

              <div class="mint-bottom__item">
                <p class="mint-bottom__text">VIEW ORIGINAL</p>
              </div>

              <div class="mint-bottom__item">
                <SocialsComp/>
              </div>
            </div>
          </div>
        </div>
			</div>
		</header>
	</div>

  <PopupComp
    v-model:is-open="checkoutPopup"
    :popup-title="'Checkout'"
  >
    <CheckoutComp
      :user-id="userId"
      :contract-price="contractPrice"
      :mint-quant="mintQuant"
      :triggerPopup="checkoutPopup"
      @disconnect="disconnect"
      @close-me="closePopup"
    />
  </PopupComp>
</template>

<script>
import config from '@/config'
import NavComp from '@/components/nav-comp.vue'
import Choices from 'choices.js'
import SocialsComp from '@/components/socials-comp.vue'
import wallets from '@/wallets/wallets'
import PopupComp from '@/components/popup-comp.vue'
import CheckoutComp from '@/components/checkout-comp.vue'
import { fixedPrice } from '@/functions'

export default {
  name: 'App',

  components: {
    NavComp,
    SocialsComp,
    PopupComp,
    CheckoutComp,
  },

  data() {
    return {
      config: config,
      imageInterval: null,
      mainImageId: 1,
      isConnect: false,
      userId: false,
      contractPrice: false,
      contractMinted: false,
      checkoutPopup: false,
      mintQuant: 1,
      freeAirdropped: [
        { mint: 10, free: 2 },
        { mint: 15, free: 3 },
        { mint: 20, free: 4 },
        { mint: 25, free: 5 },
        { mint: 30, free: 6 },
        { mint: 35, free: 7 },
        { mint: 40, free: 8 },
        { mint: 45, free: 9 },
        { mint: 50, free: 10 },
        { mint: 55, free: 11 },
        { mint: 60, free: 12 },
        { mint: 65, free: 13 },
        { mint: 70, free: 14 },
        { mint: 75, free: 15 },
        { mint: 80, free: 16 },
        { mint: 85, free: 17 },
        { mint: 90, free: 18 },
        { mint: 95, free: 19 },
        { mint: 100, free:  20 },
      ]
    }
  },

  mounted() {
    new Choices(this.$refs.choice, {
      searchEnabled: false,
    })

    this.checkConnect()
    this.mainimageRandom()
  },

  beforeUnmount() {
    clearInterval(this.imageInterval)
  },

  methods: {
    setConnectStatus(status) {
      this.isConnect = status
      this.getContractData()
    },

    async checkConnect() {
      let rezult = await wallets.checkConnectWallet()
      this.userId = rezult
      this.isConnect = rezult !== false
      this.getContractData()
    },

    async connectWallet() {
      let rezult = await wallets.connect()

			if(!rezult) {
				console.error('Wallet connect error')
				return
			}
      this.isConnect = true
      this.userId = rezult
      this.getContractData()
    },

    async getContractData() {
      if(!this.isConnect || !this.userId) {
        this.contractPrice = false
        this.contractMinted = false
        return false
      }
      this.contractPrice = await wallets.getPrice()
      this.contractMinted = await wallets.getMinted()
      return true
    },

    disconnect() {
      wallets.disconnect()
      this.isConnect = false
      this.userId = false
      this.checkoutPopup = false
    },

    buyNow() {
      this.checkoutPopup = true
      this.getContractData()
    },

		fixedPrice(summ) {
			return fixedPrice(summ)
		},

    closePopup() {
      this.checkoutPopup = false
    },

    mainimageRandom() {
      this.imageInterval = setInterval(() => {
        const limit = { min: 1, max: 29 }
        this.mainImageId = Math.floor(Math.random() * ((limit.max + 1) - limit.min) + limit.min)
      },600)
    }
  },

  computed: {
    free() {
			let free = 0
			if(this.mintQuant >= 10) free = Math.round(this.mintQuant / 5)
			return free
		},

    totalPrice() {
      if(this.contractPrice) {
        let count = this.mintQuant - this.free
        return this.fixedPrice(Number(this.contractPrice) * count)
      }
      return config.defaultPrice
		},

    ntfPrice() {
      if(this.contractPrice) {
        return this.fixedPrice(Number(this.contractPrice) * Number(this.mintQuant))
      }
      return '0.00'
    }
  }
}
</script>

